li {
  list-style: none;
}

p {
  font-size: 24px;
  font-family: monospace;
}

.Container {
  width: 400px;
  padding: 10px;
  border: 1px solid black;
  text-align: center;
}

.controls>button {
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
  background-color: goldenrod;
  color: #fff;
  font-weight: 500;
  margin-left: 5px;
  margin-right: 5px;
  font-family: inherit;
  cursor: pointer;
  outline: none;
  box-shadow: 1px 2px 2px 0 rgb(0 0 0 / 0.5);
  transition: box-shadow 250ms linear;
}

.controls>button:active {
  box-shadow: 2px 5px 2px 0 rgb(0 0 0 / 0.5);
}

.value {
  font-size: 24px;
  font-family: monospace;
  text-align: start;
  margin-top: 5px;
}
